import React, { Component } from 'react';
import 'Assets/CSS/App.css';
import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'

class Landing extends Component {  //Pagina iniziale

  render() {
    return (
			<div style={{textAlign: 'center'}}>
          <h1 className='landingTitle'>MSearch</h1>
          <h2 className = 'landingSubtitle'>Benvenuto nell'MSearch, lo strumento di analisi dei trattati online.</h2>
          <p className = 'landingExplanation'>Ti guiderò nella tua prima query di ricerca.
          Noterai che la querybar qui sopra si modifica automaticamente per riflettere i parametri che imposteremo insieme.
          Puoi saltare questi passaggi quando vuoi semplicemente digitando nel linguaggio dell'MSearch nella querybar.
          Se preferisci, puoi anche compilare una query parziale con la procedura guidata e rifinirla manualmente.
          <br />Sei pronto?</p>
          <Button
            variant = 'raised'
            color = 'secondary'
            onClick = {()=>this.props.handleNavigation('command')}
          >
            Iniziamo!
          </Button>
      </div>
    )

  }


}

export default Landing



