import React, { Component } from 'react';
import 'Assets/CSS/App.css';
import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

class CommandPage extends Component {

	render() {
		return (
			<Grid container>
        <Grid item xs={4} style = {{textAlign: 'center'}}>
          <Button variant = 'raised' color ='secondary'
            onClick = {()=>{
							this.props.handleRead('command', 'view');
							this.props.handleNavigation('treatises');
						}}
          >
            VIEW
          </Button>
          <Typography className = 'text' style = {{marginTop: 15, textAlign: 'center'}}>{'Visualizza una parte di uno o più trattati'}</Typography>
        </Grid>
        <Grid item xs={4} style = {{textAlign: 'center'}}>
          <Button variant = 'raised' color ='secondary'
            onClick = {()=>{
							this.props.handleRead('command', 'search')
							this.props.handleNavigation('treatises')
						}}
          >
            SEARCH
          </Button>
          <Typography className = 'text' style = {{marginTop: 15, textAlign: 'center'}}>{'Cerca nei trattati'}</Typography>
        </Grid>
        <Grid item xs={4} style = {{textAlign: 'center'}}>
          <Button variant = 'raised' color ='secondary' disabled>
            CONTEXT
          </Button>
          <Typography className = 'text' style = {{marginTop: 15, textAlign: 'center'}}>{'Coming soon'}</Typography>
        </Grid>
      </Grid>
		)
	  }
}

export default CommandPage



