import React, { Component } from 'react';
import 'Assets/CSS/App.css';
import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import 'Assets/CSS/wizard.css'

class Ending extends Component {  //Pagina iniziale

  render() {
    return (
			<div>
        <h2 className = 'endingMaintitle'>I risultati della tua query sono disponibili qui sotto</h2>
        <h3 className = 'endingPill1'>Usa il menu di navigazione a destra per muoverti velocemente tra i risultati</h3>
        <h3 className = 'endingPill2'>Ricorda che puoi salvarre i tuoi risultati premendo "scarica PDF" in basso a destra</h3>
      </div>
    )

  }


}

export default Ending



