import React, { Component } from 'react';
import 'Assets/CSS/App.css';
import 'Assets/CSS/Results.css';
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Hidden from '@material-ui/core/Hidden'
import Toolbar from '@material-ui/core/Toolbar'
import CircularProgress from '@material-ui/core/CircularProgress';
//Extractors
import IndexExtractor from 'Pages/Extractors/IndexExtractor.js'
import TextExtractor from 'Pages/Extractors/TextExtractor.js'
import TextPDFExtractor from 'Pages/Extractors/TextPDFExtractor.js'
import {animateScroll as scroll} from 'react-scroll'
//Import to generate the pdf
import jsPDF from 'jspdf';
let doc = new jsPDF();

/*to block user selection
CSS: -webkit-user-select: none;
     -moz-user-select: -moz-none;
      -ms-user-select: none;
          user-select: none;
React: onCopy, onCut messo su tutti i div che modifica il testo copiato (properietà clipboardData) in un "Copyright SwordAcademy 2018. Per ottenere i risultati della tua ricerca, usa il pulsante Download"
onSelect c'è un prevent default possibile


to create the downloadable pdf:
https://parall.ax/products/jspdf
https://github.com/MrRio/jsPDF
è lo stesso?
Imparare bene la libreria perché viene comoda!
*/

class Results extends Component {

  generatePDF(){
    let elementHandler = {'#ignorePDF': function (element, renderer) {return true}}
      //this can be used to generate the pdf from a dangerourHTML.string
    let textPDF = TextPDFExtractor({...this.props})
      //This take the text from the page
    let source = document.getElementById('content')
      //Change source/textPDF to choose
    doc.fromHTML(source, 15, 15, {'width': 180,'elementHandlers': elementHandler})
    doc.save('a4.pdf')
  }

  render() {
    if (this.props.loading)
      return (<Loading />)
    else
      return (
          <Grid container spacing={8}>
            <Grid item sm={8} xs={12} className='results'>
              <div id='content'>
        	      <TextExtractor {...this.props} />
              </div>
            </Grid>
            <Hidden xsDown>
              <Grid item sm={4}>
                <div className='sidenav'>
                <h4 className='indexTitle'>INDICE</h4>
                  <IndexExtractor {...this.props} />
                </div>
                <Toolbar className = 'navFooter'>
                  <Button variant = 'raised' color ='secondary'
                          onClick={() => this.generatePDF()}
                  >
                    Scarica PDF
                  </Button>
                  <Button variant = 'flat'>Segnala un problema</Button>
                  <Button variant = "flat" onClick = {scroll.scrollToTop}>To top</Button>
                </Toolbar>
              </Grid>
            </Hidden>
          </Grid>
      )
  }
}

function Loading (props) {
  return (
    <div className = 'loadingScreen'>
      <div>
        <CircularProgress size={50} color= 'secondary'/>
        <h1 className = 'loadingTitle'>Loading</h1>
        <h2 className = 'loadingSubtitle'>Go kill some goblins and come back in a minute!</h2>
      </div>
    </div>
  )
}
 export default Results;



